import Vue from 'vue'
import VueI18n from 'vue-i18n'

// json    function
const requireLang = require.context(
    '@/lang',
    true,
    /\.json$/
)
const messages = {}

// json file read
for (const file of requireLang.keys()) {
    if (file === './index.js') continue // index.js  
    const path = file.replace(/(\.\/|\.json$)/g, '').split('/')
    path.reduce((o, s, i) => {
        if (o[s]) return o[s]
        o[s] = i + 1 === path.length ? requireLang(file) : {}
        return o[s]
    }, messages)
}

Vue.use(VueI18n)

let defaultLocale = () => {
    localStorage.getItem('lang') ? localStorage.getItem('lang') : 'ko'
}

const i18n = new VueI18n({
    locale: defaultLocale, //  locale
    fallbackLocale: 'ko', // locale    locale
    messages, //  
    silentTranslationWarn: true //     console 
})

export default i18n
